import { Component, Element, h, Host, Prop } from '@stencil/core'

@Component({
  tag: 'ato-col',
  shadow: false,
})
export class AtoCol {
  @Prop() size?: 'auto' | string
  @Prop() sizeSm?: string
  @Prop() sizeMd?: string
  @Prop() sizeLg?: string
  @Prop() sizeXl?: string
  @Prop() offset?: string
  @Prop() offsetSm?: string
  @Prop() offsetMd?: string
  @Prop() offsetLg?: string
  @Prop() offsetXl?: string
  @Prop() push?: string
  @Prop() pull?: string

  @Element() private element: HTMLElement

  componentDidLoad() {
    const ionCol = document.createElement('ion-col')
    ionCol.setAttribute('size', this.size)
    ionCol.setAttribute('size-sm', this.sizeSm)
    ionCol.setAttribute('size-md', this.sizeMd)
    ionCol.setAttribute('size-lg', this.sizeLg)
    ionCol.setAttribute('size-xl', this.sizeXl)
    ionCol.setAttribute('offset', this.offset)
    ionCol.setAttribute('offset-sm', this.offsetSm)
    ionCol.setAttribute('offset-md', this.offsetMd)
    ionCol.setAttribute('offset-lg', this.offsetLg)
    ionCol.setAttribute('offset-xl', this.offsetXl)
    ionCol.setAttribute('push', this.push)
    ionCol.setAttribute('pull', this.pull)

    const parent = this.element.parentElement
    if (parent) {
      parent.replaceChild(ionCol, this.element)
      ionCol.appendChild(this.element.childNodes[0])
    }
  }

  render() {
    return (
      <Host>
        <slot />
      </Host>
    )
  }
}
